.app-button
  padding: 10px 15px
  border-radius: 3px
  background-color: #a2c5ec
  margin: 10px

.button-link
  padding: 10px 15px
  border-radius: 3px
  background-color: #a2c5ec
  margin: 10px
  text-decoration: none
  border: 1px solid #000
  color: #222222

//Header

.page-header
  display: flex
  justify-content: space-between
  min-height: 75px
  padding: 18px 0
  box-sizing: border-box
  background: #222222
  margin: 0 0 40px 0
  flex: 0 0 auto

.page-header__logo
  display: flex
  align-items: center

.page-header__logo-txt
  color: #FFFFFF
  font-size: 35px
  line-height: 38px
  font-weight: 600
  display: block
  margin-left: 10px

.page-header__logo-txt:hover,
.page-header__menu a:hover
  color: aqua

.page-header__logo-txt:hover,
.page-header__menu a:hover
  fill: lightblue

.page-header__logo > a
  padding-top: 7px

.page-header__menu
  display: flex
  align-items: center

.page-header__menu > a
  color: #FFFFFF
  font-size: 35px
  line-height: 38px
  font-weight: 600
  text-decoration: none
  margin: 0 25px

.page-header__icons-wrp
  display: flex
  align-items: center

.page-header__icons-logout
  background: none
  border: none

.page-header__icons-username
  color: white
  text-transform: capitalize
  font-size: 28px
  line-height: 31px
  font-weight: 400

.page-header__icons svg
  width: 37px
  height: 37px
  fill: #FFFFFF
  text-decoration: none
  transition: transform 500ms ease
  padding: 0 10px

.page-header__icons:hover svg
  fill: aqua
  transform: scale(1.3)

.page-header__icons:active svg
  fill: lightblue

// Footer

.page-footer
  display: flex
  justify-content: space-between
  align-items: center
  min-height: 79px
  box-sizing: border-box
  background-color: #222224
  padding: 0 14%
  margin-top: 80px

.page-footer__copy-txt
  font-style: normal
  font-weight: 400
  font-size: 16px
  line-height: 19.2px
  color: #FBFBFB

.page-footer__socials
  display: flex
  justify-content: space-between
  align-items: center
  width: 148px

.page-footer__socials-item
  display: flex
  justify-content: center
  align-items: center
  width: 32px
  height: 32px
  background-color: #fff

.page-footer__socials-item svg
  fill: #000

.page-footer__socials-item:hover svg
  fill: aqua

.page-footer__socials-item:active
  background-color: aqua

.page-footer__socials-item:active svg
  fill: #fff

.icon-instagram:hover
  background-color: aqua

.icon-instagram:hover svg
  fill: #FFFFFF

.page-notFound
  display: flex
  justify-content: center

.page-notFound > h2
  font-size: 35px
  line-height: 38px
  font-weight: 600

.page-notFound span
  color: red

.active
  color: #679dd7 !important

.project-details
  font-size: 20px
  font-weight: 400
  line-height: 24px

.project-details span
  display: block
  margin: 20px 0
  font-weight: bold

.project-details__name
  display: block
  margin: 50px 0

.project-details__repository
  margin-left: 15px

.project-details__list-user li
  text-transform: capitalize

.page-auth__form
  display: flex
  justify-content: center

.page-auth__form-field
  border: none
  display: flex
  flex-direction: column

.page-auth__form-input
  height: 45px
  margin-bottom: 30px
  text-indent: 17px
  padding: 0
  border: 0
  outline: 1px solid #A4A4A4

.page-auth__form-submit
  padding: 15px 56px
  align-self: center

.page-auth__form-title
  text-align: center
  font-size: 35px
  line-height: 38px
  font-weight: 500
  margin-bottom: 50px

.page-create-form
  display: flex
  justify-content: center

.create-form
  display: flex
  flex-direction: column
  padding: 20px

.create-form-div
  padding: 10px 0

.create-form-label
  display: block
  width: 200px
  margin-bottom: 3px

.create-form-input
  width: 200px

.create-form-select
  width: 200px

.projects__bar
  display: flex
  justify-content: space-between
  align-items: center
  margin: 0 80px

.search__input
  height: 35px
  background-color: #a2c5ec
  color: #FFFFFF
  border: 1px solid #222222
  border-radius: 3px

  &::placeholder
    color: #FFFFFF

.padding-site
  padding-left: 8%
  padding-right: 8%
